  h4{font-size: 14px;}
  img{position: absolute;right: 5px;top: 50%;transform: translateY(-50%);width: 70px;height: 70px;border-radius: 10px;}
   &>span{color: rgba(127,210,255,0.8);position: absolute;top: -20px;right: 0px;font-size: 11px;}

  .tweet__acticons{position: absolute;bottom: -25px;left:0;
     span{color: rgba(127,210,255,0.8);padding: 0 5px;
      cursor: pointer;
       }
      }
      .tweetEdit{
        .formBtn{cursor: pointer;margin-top: 15px;margin-bottom: 5px;}
      }
